.card {
  background: #fff;
  border-radius: 2px;
  height: 180px;
  margin: 1rem;
  position: relative;
  width: 180px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#fff9f5;
}
.card > .img-container > img {
  width: 100%
}
.card:hover{
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  cursor:pointer;
}


